<template>
  <ul class="blog-ul">
    <li class="new-one">
      <p class="title" @click="createNewBlog">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-add" />
        </svg>
        
      </p>
      <p class="tags">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-tags" />
        </svg>
      </p>
    </li>
    <li v-for="item in blogs" :key="item.title" :class="['blog-li',{active:item.id === activeId}]">
      <p class="title" @click="editBlog(item.id)">{{item.title}}</p>
      <p class="tags">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-tags" />
        </svg>
        {{item.tags.map(e=>e.tag).join(',')}}
      </p>
      <div class="options">
        <svg class="icon" aria-hidden="true" @click="initDelete(item.id)">
          <use xlink:href="#icon-delete" />
        </svg>
      </div>
    </li>
    <v-dialog v-model="deleteDialog" max-width="290">
      <v-card>
        <v-card-title class="headline"></v-card-title>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="red " text @click="deleteArticle"></v-btn>
          <v-btn color="green " text @click="deleteDialog = false"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </ul>
</template>

<script>
export default {
  props: {
    blogs: {
      type: Array,
      default: () => []
    },
    activeId: {
      type: Number,
      default: null
    }
  },
  data: () => ({
    deleteDialog: false,
    deleteId: null
  }),
  methods: {
    initDelete(id) {
      this.deleteId = id;
      this.deleteDialog = true;
    },
    async deleteArticle() {
      try {
        let deleteRes = await this.$axios.deleteArticle({
          id: this.deleteId
        });
        this.deleteId = null;
        this.deleteDialog = false;
        // TODO: 
        this.$emit("update", true);
      } catch (e) {
        console.log(e);
      }
    },
    remove(item) {
      const index = this.friends.indexOf(item.name);
      if (index >= 0) this.friends.splice(index, 1);
    },
    createNewBlog() {
      this.$emit("editBlog", null);
    },
    editBlog(id) {
      this.$emit("editBlog", id);
    }
  }
};
</script>

