    border-bottom: 1px dashed #ccc;
    p {
      margin: 0;
    }
    .active {
      background-color: #eee;
    }
    .title {
      color: rgb(73, 73, 73);
      margin-bottom: 10px;
      &:hover {
        color: #000;
      }
    }
    .tags {
      font-size: 12px;
      color: #666;
    }
    .options {
      top: 16px;
      right: 10px;
      position: absolute;
      font-size: 14px;
      svg {
        cursor: pointer;
      }
    }
  }
}
